<template>
  <div>
    <div class="tablebox">
      <ImageButton :checkImage="checkImage" @delsuccess="fetchImageList" @success="fetchImageList"></ImageButton>
      <SerchInput :serchdata="imageList" @serchActive="serchActive"></SerchInput>
      <ImageTable v-if="imageList" :imageList="AfterChangeData" @changecheck="changecheck"></ImageTable>
    </div>
    <div class="page" v-if="imageList">
      <div class="pagenum">
        <el-select v-model="pageNum" :placeholder="plsSelectActiveRange " style="width: 55px" size="mini">
          <el-option :label="item" :value="item" v-for="item in Math.ceil((serchList ? serchList.length : imageList.length)/pageSize)" :key="item"></el-option>
        </el-select>
        {{ $t('Page_Page') }}
      </div>
      <el-pagination
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="pageNum"
        :page-size="pageSize"
        layout="prev, pager, next"
        :total="serchList ? serchList.length : imageList.length"
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import { Form, FormItem, Select, Option, Input, Button, Popover, Table, TableColumn, Pagination } from 'element-ui'
import post from '@/utils/post'
import SeniorAdd from '@/components/Image/SeniorAdd'
import SimpleAdd from '@/components/Image/SimpleAdd'
import ImageTable from '@/components/Image/Table'
import ImageButton from '@/components/Image/ImageButton'
import SerchInput from '@/components/Image/SerchInput'
import SerchImage from '@/components/Image/SerchImage'
Vue.use(Form)
Vue.use(FormItem)
Vue.use(Select)
Vue.use(Input)
Vue.use(Option)
Vue.use(Button)
Vue.use(Popover)
Vue.use(Table)
Vue.use(TableColumn)
Vue.use(Pagination)

export default {
  data () {
    return {
      addShow: 0, // 控制高级拉取还是简单拉取镜像
      imageList: null, // 请求的镜像数据
      checkImage: [], // 选择的镜像列表
      serchList: null, // 搜索匹配的数据列表
      pageNum: 1, // 当前页
      pageSize: 6, // 每页显示数量
      plsSelectActiveRange:'',
    }
  },
  mounted () {
    // 初始请求镜像列表
    this.fetchImageList()
  },
  computed: {
    // 返回当前页面显示的数据
    AfterChangeData () {
      let start = (this.pageNum - 1) * this.pageSize
      let end = this.pageNum * this.pageSize
      return (this.serchList ? this.serchList.slice(start, end) : this.imageList.slice(start, end))
    }
  },
  methods: {
    // 分页大小改变
    handleSizeChange (val) {
      this.pageSize = val
    },
    // 当前页数改变
    handleCurrentChange (val) {
      this.pageNum = val
    },
    // 显示高级添加还是简单添加
    changeAddShow (id) {
      this.addShow = id
    },
    // 处理选中容器
    changecheck (item) {
      this.checkImage = item
    },
    // 获取镜像列表
    fetchImageList () {
      const docker = 'curl --unix-socket /var/run/docker.sock -s "http:/v1.40/images/json?all=true"' // 查询
      post({ docker }, (res) => {
        // console.log(res)
        this.imageList = res
      }, this)
    },
    // 处理搜索穿回来的值
    serchActive (item) {
      this.serchList = item
    }
  },
  components: {
    SeniorAdd,
    SimpleAdd,
    ImageTable,
    ImageButton,
    SerchInput,
    SerchImage
  }
}
</script>

